import '@equinor/echo-components/dist/index';
import { EventHubProvider } from '@equinor/echo-core';
import { EchoContent, EchoEventHandler, EchoRoutes, Toasters } from '@equinor/echo-framework';
import React from 'react';
import { Redirect, Route, Switch } from 'react-router-dom';
import { Home } from './components/home/home';
import { Legend } from './components/legend';

export const EchoApp: React.FC = (): JSX.Element => {
    return (
        <>
            <EventHubProvider>
                <EchoEventHandler>
                    <EchoContent Legend={Legend}>
                        <Switch>
                            <Route exact path={'/'} component={Home} />
                            <EchoRoutes />
                            <Route render={(): JSX.Element => <Redirect to="/" />} />
                        </Switch>
                        <Toasters />
                    </EchoContent>
                </EchoEventHandler>
            </EventHubProvider>
        </>
    );
};
